{
    display: flex;
    width: 60vw;
    height: 15vh;
    font-size: 20px;
    justify-content: space-evenly;
    align-items: center;
}
.link-navbar
{
    position: relative;
    text-decoration: none;
    color: white;
    font-weight: bolder;
     display: inline-block;
    transition: 0.4s;
}
.click::after
{
    content: '';
    position: absolute;
    width: 100%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    height: 5px;
    background-color:#ffc400;
    left: 0;
    bottom: -10%;
    border-radius: 25px;
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    transition: -webkit-transform 0.25s ease-out;
    transition: transform 0.25s ease-out;
    transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}
.click:hover::after
{
     -webkit-transform: scaleX(1);
             transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}

.link-navbar > img
{
    height: 120px;
}

.sticky
{
    position: fixed;
}
.navbar-sticky
{
    z-index: 9999;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 5vh;
    visibility: visible;
    position: fixed;
    color: rgba(255, 255, 255, 0);
    background: linear-gradient(18.83deg, #96502b 50.21%, #69422d 100%)
}
.visible
{
    background-color: rgba(165, 42, 42, 0);
    visibility: hidden;
    position: absolute;
    top: -100%;
}

.navbar-phone-container
{
    z-index: 999999;
